import React, { useEffect, useState } from "react";
import "../css/Contact.css";
import axios from "../../axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Contact = () => {
  const [contact, setContact] = useState({
    username: "",
    email: "",
    message: "",
  });

  const contectPage = async () => {
    try {
      const res = await axios.get("/contact");
      setContact({
        ...contact,
        username: res.data.username,
        email: res.data.email,
      });
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    contectPage();
  }, []);

  const Change = (e) => {
    const name = e.target.name;
    const value = e.target.value;

    setContact({ ...contact, [name]: value });
  };

  const Submit = async (e) => {
    e.preventDefault();
    try {
      const contactPost = await axios.post("/contact", contact);
      if (contactPost) {
        toast.info(contactPost.data.message, {
          position: "top-center",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
      setContact({ ...contact, message: "" });
    } catch (error) {
      const contactError = JSON.parse(error.request.response);
      toast.error(contactError.message, {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: false,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  return (
    <div className="container-fluid">
      <div className="row">
        <div className="col-md-8 col-12 mx-auto Contact">
          <h1 className="text-center mb-4 contact_border">Contact</h1>
          <form onSubmit={Submit} method="POST" autoComplete="off">
            <div className="row">
              <div className="col-md-6">
                <div className="input-group mb-4">
                  <input
                    placeholder="Username"
                    onChange={Change}
                    value={contact.username}
                    required
                    type="text"
                    name="username"
                    className="form-control inputs"
                  />
                </div>
              </div>
              <div className="col-md-6">
                <div className="input-group mb-4">
                  <input
                    placeholder="E-mail"
                    onChange={Change}
                    value={contact.email}
                    required
                    type="email"
                    name="email"
                    className="form-control inputs"
                  />
                </div>
              </div>
            </div>
            <div className="input-group mb-4">
              <input
                placeholder="Type Your Mesaage Here..."
                name="message"
                value={contact.message}
                onChange={Change}
                className="form-control inputs"
                required
              />
            </div>
            <div className="input group mb-4">
              <input type="submit" value="Send" className="btn btn-primary" />
            </div>
          </form>
        </div>
      </div>
      <ToastContainer />
    </div>
  );
};

export default Contact;
