import React, { useContext, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "../../axios";
import "../css/Login.css";
import { ToastContainer, toast } from "react-toastify";
import { AiFillCloseCircle } from "react-icons/ai";

import { UserContext } from "../../App";

const errorIconStyle = {
  fontSize: "1.5rem",
};

const Login = () => {
  const { dispatch } = useContext(UserContext);

  const [Login, setLogin] = useState({
    username: "",
    password: "",
  });

  const history = useHistory();

  const Change = (e) => {
    const name = e.target.name;
    const value = e.target.value;

    setLogin({ ...Login, [name]: value });
  };

  const Submit = async (e) => {
    try {
      e.preventDefault();

      await axios.post("/login", Login);

      dispatch({ type: "LoggedIn", payload: true });

      history.push("/about");
    } catch (error) {
      const errorMessage = JSON.parse(error.request.response);
      toast.error(
        <p className="mb-0">
          <AiFillCloseCircle style={errorIconStyle} className="me-2" />
          {errorMessage.message}
        </p>,
        {
          position: "top-center",
          closeOnClick: false,
          pauseOnFocusLoss: true,
          pauseOnHover: true,
        }
      );
    }
  };

  return (
    <div className="container-fluid">
      <div className="row">
        <div className="col-md-8 col-12 mx-auto Login ">
          <form method="post" onSubmit={Submit} autoComplete="off">
            <h1 className="text-center mb-4">Login</h1>
            <div className="input-group mb-3">
              <input
                name="username"
                type="text"
                value={Login.username}
                onChange={Change}
                placeholder="Username"
                required
                className="form-control  inputs"
              />
            </div>
            <div className="input-group mb-3">
              <input
                name="password"
                type="password"
                value={Login.password}
                onChange={Change}
                placeholder="Password"
                required
                className="form-control inputs"
              />
            </div>

            <p className="text-center">
              Don't Have An Account?
              <Link to="/signup"> Signup</Link>
            </p>

            <div className="input-group mb-3">
              <input
                type="submit"
                value="Login"
                className="btn btn-primary mx-auto"
              />
            </div>
          </form>
        </div>
      </div>
      <ToastContainer />
    </div>
  );
};

export default Login;
