import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from "../../axios";
import mrsaad from "../images/mrsaad.svg";
import "../css/About.css";

const About = () => {
  const [About, setAbout] = useState();
  const history = useHistory();

  const contectPage = async () => {
    try {
      const res = await axios.get("/contact");
      setAbout({
        ...About,
        username: res.data.username,
        email: res.data.email,
      });
    } catch (error) {
      console.error(error);
      history.push("/login");
    }
  };

  useEffect(() => {
    contectPage();
  }, []);
  return (
    <>
      <div className="container-fluid About">
        <div className="row">
          <div className="col-md-6 col-12 text-center header_text">
            <h1 className="saad">Saad Khatri</h1>
            <p className="">MERN Stack Developer</p>
          </div>
          <div className="col-md-6 col-12 bg-ligh d-flex justify-content-center image">
            <img src={mrsaad} alt="mrsaad" />
          </div>
        </div>
      </div>
    </>
  );
};

export default About;
