import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import "../css/Register.css";
import axios from "../../axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { AiFillCloseCircle } from "react-icons/ai";

const errorIconStyle = {
  fontSize: "1.5rem",
};

const Register = () => {
  const history = useHistory();

  const [Registration, setRegistration] = useState({
    username: "",
    email: "",
    mobile: "",
    password: "",
    cpassword: "",
  });
  const Change = (e) => {
    const name = e.target.name;
    const value = e.target.value;

    setRegistration({ ...Registration, [name]: value });
  };

  const Submit = async (e) => {
    e.preventDefault();

    try {
      await axios.post("/register", Registration);
      history.push("/login");
    } catch (error) {
      const errorMessage = JSON.parse(error.request.response);
      console.log(errorMessage);
      toast.error(
        <p className="mb-0">
          <AiFillCloseCircle style={errorIconStyle} className="me-2" />
          {errorMessage.message}
        </p>,
        {
          position: "top-center",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        }
      );
    }
  };

  return (
    <div className="container-fluid my-5">
      <div className="row">
        <div className="col-md-8 col-12 mx-auto Register">
          <div className="row parent">
            <div className="col-md-6 col-12">
              <h1 className="text-center">Sign Up</h1>
              <div className="register">
                <form onSubmit={Submit} method="POST" autoComplete="off">
                  <div className="input-group">
                    <input
                      type="text"
                      className="form-control inputs my-3"
                      placeholder="Username"
                      required
                      name="username"
                      value={Registration.username}
                      onChange={Change}
                    />
                  </div>
                  <div className="input-group">
                    <input
                      type="email"
                      className="form-control inputs my-3"
                      placeholder="E-mail"
                      required
                      name="email"
                      value={Registration.email}
                      onChange={Change}
                    />
                  </div>
                  <div className="input-group">
                    <input
                      type="number"
                      minLength="10"
                      maxLength="12"
                      className="form-control inputs my-3"
                      placeholder="Mobile"
                      required
                      name="mobile"
                      value={Registration.mobile}
                      onChange={Change}
                    />
                  </div>
                  <div className="input-group">
                    <input
                      type="password"
                      className="form-control inputs my-3"
                      placeholder="Password"
                      required
                      name="password"
                      value={Registration.password}
                      onChange={Change}
                    />
                  </div>
                  <div className="input-group">
                    <input
                      type="password"
                      className="form-control inputs my-3"
                      placeholder="Confirm Password"
                      required
                      name="cpassword"
                      value={Registration.cpassword}
                      onChange={Change}
                    />
                  </div>

                  <div className="input-group">
                    <input
                      type="submit"
                      className="btn btn-primary mx-auto mb-2"
                      value="Register"
                    />
                  </div>
                  <p className="text-center">
                    Already Have An Account?
                    <Link to="/login"> Login</Link>
                  </p>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <ToastContainer />
    </div>
  );
};

export default Register;
