import React, { useState, useEffect, useContext } from "react";
import { useHistory } from "react-router-dom";
import "./css/Comments.css";
import axios from "../axios";
import { toast, ToastContainer } from "react-toastify";
import { AiFillDownCircle, AiFillUpCircle } from "react-icons/ai";

import { UserContext } from "../App";

const CommentsContainer = () => {
  const [Comments, setComments] = useState([]);
  const [username, setUsername] = useState("");
  const [comment, setComment] = useState("");
  const [showMore, setShowMore] = useState(false);

  const { state, dispatch } = useContext(UserContext);

  const history = useHistory();

  const getComments = async () => {
    const res = await axios.get("/comments");
    setComments(res.data);
  };

  const getData = async () => {
    try {
      const res = await axios.get("/navbar");
      setUsername(res.data.username);
      dispatch({ type: "LoggedIn", payload: true });
    } catch (error) {
      console.error(error);
    }
  };
  const DeleteComment = async (id) => {
    try {
      await axios.get(`/deleteComment/${id}`);
      getComments();
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    getComments();
  }, []);

  useEffect(() => {
    getData();
  }, [state]);

  const Submit = async (e) => {
    e.preventDefault();
    const data = {
      username,
      comment,
    };
    try {
      if (!state) {
        window.scrollTo(0, 0);
        toast.info("You Must Need to Login Before Leaving a Comment", {
          position: "top-center",
          autoClose: 6000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
        });
        return history.push("/login");
      }

      await axios.post("/comment", data);
      getComments();

      setComment("");
    } catch (error) {
      console.error(error);
    }
  };

  const handleClick = () => {
    setShowMore(true);

    if (showMore) {
      setShowMore(false);
    }
  };

  const number = showMore ? Comments.length : 2;
  return (
    <div className="container-fluid comment_container">
      <div className="col-md-10 col-12 mx-auto">
        <form onSubmit={Submit} method="POST" autoComplete="off">
          <h2 className="comment_text my-4">Comments</h2>
          <div className="input-group my-3">
            <input
              type="hidden"
              placeholder="Username"
              name="username"
              value={username}
            />
          </div>
          <div className="input-group my-3">
            <input
              required
              type="text"
              className="form-control"
              placeholder="Leave a Comment..."
              value={comment}
              name="comment"
              onChange={(e) => setComment(e.target.value)}
            />
          </div>
          <div className="input-group my-2">
            <input type="submit" className="btn btn-primary" value="Post" />
          </div>
          <div className="comments my-5">
            {Comments.slice(0, number).map((all, ind) => {
              return (
                <div className="comment my-2" key={ind}>
                  <div className="username">{all.username}</div>
                  <span> :</span>
                  <br />
                  <span className="user_comment">{all.comment}</span>
                  <input
                    type="button"
                    value="Delete"
                    className="btn btn-danger float-end"
                    onClick={() => DeleteComment(all._id)}
                  />
                </div>
              );
            })}
            <button
              type="button"
              className="btn btn-primary mt-5 d-block mx-auto"
              onClick={handleClick}
            >
              {showMore ? (
                <span>
                  Show Less <AiFillUpCircle />
                </span>
              ) : (
                <span>
                  Show More <AiFillDownCircle />
                </span>
              )}
            </button>
          </div>
        </form>
      </div>
      <ToastContainer />
    </div>
  );
};

export default CommentsContainer;
